.carousel-control-next-icon,
.carousel-control-prev-icon  {
  filter: invert(1);
}

body {
    font-size: calc(10px + (26 - 14) * ((100vw - 300px) / (1600 - 200)));
    line-height: calc(1.3rem + (1.5 - 1.2) * ((100vw - 300px)/(1600 - 200)));
  }

h1{
    font-size: 2.5em;
}
h2{
    font-size: 2em;
}
h3{
    font-size: 1.75em;
}
h4{
    font-size: 1.5em;
}

h5{
    font-size: 1.25em;
}
h6{
    font-size: 1em;
}


.Snav div {
  
    transition:  3s;
    width: 200px;
  }
  
  